.header {
	&.header-admin {
		padding-left: 27px;
		padding-right: 27px;

		.header__logo {
			b {
				display: block;
				font-size: 21px;
				font-family: "Arial";
				margin-bottom: 4px;
				color: #a2a2a2;
			}

			span {
				color: #a2a2a2;
				font-size: 12px;
			}
		}

		.header__text {
			margin-left: auto;
			margin-right: auto;

			a {
				margin-right: 40px;

				&:last-child {
					margin-right: 0px;
				}
			}
		}
	}
}
