@font-face {
    font-family: 'Helios';
    src: local('HeliosLight'),
        url('../fonts/Helios/HeliosLight.woff2') format('woff2'),
        url('../fonts/Helios/HeliosLight.woff') format('woff'),
        url('../fonts/Helios/HeliosLight.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

