// Global Site Style
html, body {
	font-family: 'Helios';
	font-size: 14px;
	line-height: 1;
	color: #000000;
	height: 100%;
}

#wrapper {
	display: flex;
	flex-direction: column;
	// height: 100%;
	min-height: 100%;

	min-width: 1000px;
	overflow: auto;
}

// h3 {
// 	font-size: 25px;
// }

.container {
	width: 1180px;
	display: block;
	margin-left: auto;
	margin-right: auto;
	height: 100%;

	@media(max-width: 1220px) {
		padding-left: 20px;
		padding-right: 20px;
		width: 100%;
	}
}

.ps-container {
	.ps-scrollbar {

		&-y-rail {
			width: 3px;
			background-color: #e6e6e6;
			border-radius: 0px;
			right: 0px !important;
		}

		&-y {
			width: 5px;
			right: 0px !important;
			background-color: #b5b5b5;
			transition: .2s;
		}
	}
}




// Style Range
input[type=range] {
	height: 25px;
	-webkit-appearance: none;
	margin: 10px 0;
	width: 100%;
	background-color: transparent;
}

input[type=range]:focus {
	outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 7px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #000000;
	background: #EACED3;
	border-radius: 0px;
	border: 1px solid #D9B9BE;
}

input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px #B25D64;
	border: 4px solid #B25D64;
	height: 15px;
	width: 15px;
	border-radius: 15px;
	background: #ffffff;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -5.5px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
	background: #EACED3;
}

input[type=range]::-moz-range-track {
	width: 100%;
	height: 7px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #000000;
	background: #EACED3;
	border-radius: 0px;
	border: 1px solid #D9B9BE;
}

input[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 0px #B25D64;
	border: 4px solid #B25D64;
	height: 15px;
	width: 15px;
	border-radius: 15px;
	background: #ffffff;
	cursor: pointer;
}

input[type=range]::-ms-track {
	width: 100%;
	height: 7px;
	cursor: pointer;
	animate: 0.2s;
	background: transparent;
	border-color: transparent;
	color: transparent;
}

input[type=range]::-ms-fill-lower {
	background: #EACED3;
	border: 1px solid #D9B9BE;
	border-radius: 0px;
	box-shadow: 0px 0px 0px #000000;
}

input[type=range]::-ms-fill-upper {
	background: #EACED3;
	border: 1px solid #D9B9BE;
	border-radius: 0px;
	box-shadow: 0px 0px 0px #000000;
}

input[type=range]::-ms-thumb {
	margin-top: 1px;
	box-shadow: 0px 0px 0px #B25D64;
	border: 4px solid #B25D64;
	height: 15px;
	width: 15px;
	border-radius: 15px;
	background: #ffffff;
	cursor: pointer;
}

input[type=range]:focus::-ms-fill-lower {
	background: #EACED3;
}

input[type=range]:focus::-ms-fill-upper {
	background: #EACED3;
}


.block-range {
	margin-top: 23px;
	label {
		color: #959595;
		font-size: 14px;
		font-family: "Arial";
	}

	input {
		margin-top: 7px;
		margin-bottom: 0px;
	}
}


// --------------------------Form
input {
	height: 37px;
	width: 100%;
	border: 1px solid #e5e5e5;
	border-radius: 0px;
	box-shadow: none;
	color: #000;
	font-size: 14px;
	padding-left: 10px;
	padding-right: 10px;
	outline: none;

	&::placeholder {
		color: rgba(#000, .3);
	}

	&[type="range"] {
		border: 0px;
		padding: 0px;
	}
}

textarea {
	height: 167px;
	width: 100%;
	border: 1px solid #e5e5e5;
	border-radius: 0px;
	box-shadow: none;
	color: #000;
	font-size: 14px;
	padding-left: 10px;
	padding-right: 10px;
	outline: none;
	min-width: 100%;
	max-width: 100%;
	min-height: 167px;
	padding-top: 17px;

	&::placeholder {
		color: rgba(#000, .3);
	}
}


a {
	color: #b25d64;

	&:hover {
		text-decoration: underline;
	}
}
