		}

		svg {
			width: 25px;
			height: 25px;
			fill: #fff;
		}

		&.active {

		}
	}

	&__logo {
		margin-right: 30px;

		picture {
			display: block;
			// width: 234px;
			height: 35px;
			border: 1px solid #4d4d4d;
		}

		img {
