		}
	}

	&__link {
	}

	a {
		color: #fbc9ba;
		font-size: 12px;

		&:hover {
			text-decoration: underline;
		}
	}
}


.hamburger {
	width: 30px;
	height: 25px;
	position: relative;
	cursor: pointer;
	user-select: none;

	&.active {
		span {
			// width: 0px;
			// opacity: 0;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%) scale(0);
			opacity: 0;
		}

		&:before {
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%) rotate(45deg);
		}

		&:after {
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%) rotate(-45deg);
		}
	}

	&:after, &:before, span {
		content: "";
		display: block;
		width: 100%;
		height: 3px;
		background-color: #fff;
		display: block;
		position: absolute;
		left: 0px;
		transform-origin: center center;
		transition: .2s;
		transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
		will-change: transform, opacity, position;
	}

	&::before {
		top: 0px;
	}

	span {
		top: 50%;
		transform: translateY(-50%);
	}

	&:after {
		bottom: 0px;
	}

}
