.fancybox-bg {
	background-color: rgba(#fff, .9);
}

.popup {
	display: none;
	width: 978px;
	// box-shadow: 3px 3px 5px #000;
	border: 1px solid #e6e6e6;
	box-shadow: 5px 5px 6px -2px #9e9d9a;

	padding-left: 58px;
	padding-right: 58px;

	@media(max-width: 1080px) {
		width: 960px;
	}

	&__wrap {
		h3 {
			font-size: 26px;
			font-weight: 300;
			letter-spacing: -1px;
		}
	}
}


.selectsize {
	&__grid {
		display: flex;
		flex-wrap: wrap;
		margin-top: 62px;
	}

	&__item {
		width: 33.333%;
		padding-left: 20px;
		padding-right: 20px;
		margin-bottom: 56px;
		cursor: pointer;

		&.checked {
			i {
				&:after, &:before {
					opacity: 1;
				}



			}
		}

		i {
			display: block;
			margin-bottom: 40px;
			position: relative;

			&:after {
				content: "";
				width: 44px;
				height: 44px;
				border-radius: 100%;
				background-color: #000000;
				background: url("../ico/check.svg") center center no-repeat;

				position: absolute;
				bottom: -27px;
				left: 50%;
				transform: translateX(-50%);
				opacity: 0;
				transition: .2s;
			}

			&:before {
				content: "";
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0px;
				left: 0px;
				background-color: rgba(#fff, .777);
				opacity: 0;
				transition: .2s;
			}

			img {
				display: block;
				margin-left: auto;
				margin-right: auto;
			}
		}

		b {
			font-weight: bold;
			font-family: "Arial";
			font-size: 16px;
			display: block;
			text-align: center;
			margin-bottom: 4px;
		}

		em {
			text-align: center;
			color: #959595;
			font-family: "Arial";
			font-size: 16px;
			display: block;
			width: 100%;
		}
	}
}


.copylink {
	&__wrap {
		display: flex;
		justify-content: center;
		flex-direction: column;
		padding-top: 89px;
		padding-bottom: 36px;

		h2 {
			display: flex;
			justify-content: center;
			align-items: center;
			font-family: 'HeliosThin';
			font-weight: 100;

			margin-bottom: 62px;

			svg, img {
				width: 44px;
				height: 44px;
				margin-right: 24px;
			}

			span {
				font-weight: 300;
				font-size: 52px;
			}
		}

		> em {
			color: #959595;
			font-size: 14px;
			font-family: "Arial";
			text-align: center;
			margin-bottom: 15px;
		}

		> span {
			font-size: 23px;
			font-family: "Arial";
			text-align: center;
			margin-bottom: 80px;
		}

		button {
			border-style: solid;
			border-width: 1px;
			border-color: rgb(167, 169, 172);
			border-radius: 5px;
			background-color: rgb(149, 149, 149);
			width: 196px;
			height: 67px;
			transition: .2s;
			user-select: none;
			outline: none;
			cursor: pointer;

			margin-left: auto;
			margin-right: auto;

			&:hover {
				background-color: #000;
			}

			span {
				color: #fff;
				font-size: 23px;
				font-family: "Arial";
				text-transform: uppercase;
			}
		}
	}
}
