.constructor {
	display: flex;
	flex: 1 0 auto;

	&__menu {
		width: 189px;
		background: #f7f7f7;
		border-right: 1px solid #e5e5e5;
		height: calc(100vh - 63px);
		position: relative;

		@media(max-width: 1500px) {
			position: fixed;
			top: 63px;
			left: 0px;
			z-index: 99;
			transform: translateX(-100%);
			transition: .6s;
			// opacity: 0;
			visibility: hidden;
			will-change: transform, visibility;
			// height: calc(100% - 63px);

			&.active {
				transition: .2s;
				transform: translateX(0%);
				// opacity: 1;
				visibility: visible;
			}
		}
	}

	&__option {
		width: 267px;
		background-color: #f4f4f4;
		border-right: 1px solid #e5e5e5;

		padding-left: 23px;

		height: calc(100vh - 63px);

		@media(max-width: 1500px) {
			position: fixed;
			top: 63px;
			left: 189px;
			z-index: 98;
			// height: calc(100% - 63px);
			transform: translateX(-200%);
			opacity: 0;
			visibility: hidden;
			transition: .5s;
			will-change: transform, visibility, opacity;

			&.active {
				transform: translateX(0%);
				transition: .4s;
				// transition-delay: .1s;
				opacity: 1;
				visibility: visible;
			}
		}

	}

	&__admin {
		flex: 1;
		height: calc(100vh - 63px);
		overflow: auto;
		position: relative;
		padding-right: 20px;

		padding-left: 48px;
		padding-top: 34px;
		padding-bottom: 30px;

		@media(max-width: 1500px) {
			padding-left: 30px;
		}
	}

	&__content {
		flex: 1;
		padding-left: 4.7%;
		padding-top: 46px;
		height: calc(100vh - 63px);
		overflow: auto;
		position: relative;
		padding-right: 20px;

		@media(max-width: 1800px) {
			padding-left: 20px;
		}
	}
}

.menu {
	padding-top: 87px;

	@media(max-height: 900px) {
		padding-top: 50px;
	}

	@media(max-height: 800px) {
		padding-top: 20px;
	}

	&__item {
		margin-bottom: 12px;
		transition: .2s;
		cursor: pointer;
		user-select: none;

		&.active {
			background: #e5e5e5;
			// pointer-events: none;
		}

		&:hover {
			background: #e5e5e5;
		}

		display: block;
		cursor: pointer;
		padding-left: 27px;
		padding-top: 15px;
		padding-bottom: 15px;
		padding-right: 10px;

		span {
			display: block;
			font-size: 20px;
			font-weight: 300;
			letter-spacing: -0.8px;
			line-height: 24px;
		}

		em {
			color: #959595;
			font-size: 14px;
			margin-top: 1px;
			display: block;
			line-height: 17px;
			font-family: "Arial";
		}
	}
}

.options {
	display: none;
	height: 100%;
	overflow: hidden;

	&.active {
		display: flex;
		flex-direction: column;
	}

	&__item {
		height: 100%;
		display: flex;
		flex-direction: column;
	}

	&__title {
		width: calc(100% - 19px);
		margin-top: 46px;
		padding-bottom: 16px;
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 19px;

		span {
			font-weight: bold;
			text-transform: uppercase;
			font-size: 14px;
			font-family: "Arial";
		}
	}

	&__content {
		// height: calc(100vh - 159px);
		display: flex;
		flex-direction: column;
		flex: 1;
		height: 100%;

		&--background {
			height: calc(100% - 156px);
		}
	}

	&-background {
		.options__title {
			padding-bottom: 5px;
		}
	}

	&__add {
		padding-top: 18px;
		padding-bottom: 10px;
		display: flex;
		justify-content: center;

		span {
			width: 24px;
			height: 24px;
			border-radius: 100%;
			background-color: #000;
			position: relative;
			transition: .2s;
			transform-origin: center center;
			cursor: pointer;

			&:hover {
				transform: scale(1.1);
			}

			&:active {
				transform: scale(1);
			}

			svg {
				width: 11px;
				height: 11px;
				fill: #fff;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
}


// SVG Img
.svgimg {
	width: 100%;
	// height: 728px;
	overflow: auto;
	padding-bottom: 5px;
	border-bottom: 1px solid #e5e5e5;

	padding-right: 19px;
	position: relative;

	// @media(max-height: 900px) {
	// 	height: auto;
	// 	flex: 1;
	// }
	flex: 1;

	&__item {
		width: 100%;
		height: 61px;
		background-color: #fff;
		border: 1px solid #e5e5e5;
		margin-bottom: 5px;
	}
}

// Background
.background {
	width: 100%;
	// height: 668px;
	overflow: auto;
	padding-right: 19px;
	position: relative;
	border-bottom: 1px solid #e5e5e5;

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	// flex: 1;

	&__item {
		width: 105px;
		height: 100px;
		margin-bottom: 16px;
		border: 1px solid #efefef;
		overflow: hidden;

		cursor: pointer;
		position: relative;

		&.active {
			&:after {
				opacity: 1;
			}
		}

		&:after {
			content: "";
			display: block;
			width: 34px;
			height: 34px;
			position: absolute;
			bottom: 12px;
			left: 50%;
			transform: translateX(-50%);
			background: url("../ico/check.svg") center center no-repeat;
			opacity: 0;
			transition: .2s;
		}

		img {
			max-width: 100%;
			width: 100%;

			@supports(object-fit: over) {
				height: 100%;
				object-fit: cover;
				object-position: center center;
			}
		}
	}
}

// Scroll Block
.scrollblock {
	width: 100%;
	overflow: auto;
	padding-right: 19px;
	position: relative;
	border-bottom: 1px solid #e5e5e5;

	// height: 728px;
	// height: 100%;
	flex: 1;


	&--grid {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__item {
		width: 105px;
		height: 100px;
		margin-bottom: 16px;
		border: 1px solid #efefef;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		cursor: pointer;

		&.active {
			&:after {
				content: "";
				display: block;
				width: 34px;
				height: 34px;
				position: absolute;
				bottom: 12px;
				left: 50%;
				transform: translateX(-50%);
				background: url("../ico/check.svg") center center no-repeat;
			}
		}

		img {
			max-width: 100%;
			width: 100%;

			@supports(object-fit: over) {
				height: 100%;
				object-fit: cover;
				object-position: center center;
			}
		}
	}
}


.blocktext {
	padding-bottom: 31px;
	position: relative;
	border-bottom: 1px solid #e5e5e5;
	margin-bottom: 35px;

	&:last-of-type {
		margin-bottom: 0px;
	}

	&:last-child {
		margin-bottom: 0px;
	}

	input {
		margin-bottom: 11px;

		&:last-of-type {
			margin-bottom: 0px;
		}
	}

	svg {
		width: 14px;
		height: 14px;
		fill: #bebebe;
		position: absolute;
		bottom: 8px;
		right: 7px;
		transition: .2s;
		cursor: pointer;
		user-select: none;

		transition: .2s;
		transform-origin: center center;
		cursor: pointer;

		&:hover {
			transform: scale(1.1);
			fill: #000;
		}

		&:active {
			transform: scale(1);
		}
	}
}


.blocktextarea {
	position: relative;
	margin-bottom: 35px;


	span {
		display: flex;
		align-items: center;

		transition: .2s;
		cursor: pointer;
		user-select: none;
		transform-origin: center center;

		margin-top: 11px;
		padding-left: 8px;

		&:hover {
			// transform: scale(1.01);
			em {
				color: #000;
			}
			svg {
				fill: #000;

			}
		}

		&:active {
			// transform: scale(1);
		}

		em {
			font-size: 14px;
			color: #959595;
			transition: .2s;
		}
	}
	svg {
		width: 14px;
		height: 14px;
		fill: #bebebe;
		margin-right: 10px;
		transition: .2s;
	}
}


.canvas {
	&__link {
		margin-left: 36.9%;
		margin-bottom: 48px;

		@media(max-width: 1800px) {
			margin-left: 0px;
		}

		a {
			display: inline-flex;
			align-items: center;
			margin-right: 26px;

			img, svg {
				width: 15px;
				height: 14px;
				margin-right: 10px;
				fill: #b25d64;
			}

			span {
				text-transform: uppercase;
				font-size: 14px;
				font-family: "Arial";
				font-weight: bold;
				color: #b25d64;

			}

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__block {
		canvas {
			width: 1200px;
			height: 630px;
			border: 1px solid #e5e5e5;
			// box-shadow: 3px 3px 5px #000;
			box-shadow: 5px 5px 6px -2px #9e9d9a;
			margin-right: 20px;
		}
	}

	&__footer {
		display: flex;
		flex-direction: column;
		margin-top: 58px;
		padding-left: 2.6%;

		@media(max-width: 1800px) {
			padding-left: 0px;
		}

		span {
			margin-bottom: 23px;
			font-size: 16px;
		}

		div {
			margin-bottom: 23px;
		}

		a {
			color: #b25d64;
			font-size: 16px;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}
