.login {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background: #f7f7f7;
}

.formlog {
    width: 248px;
    text-align: center;

    h3 {
        font-size: 21px;
        font-family: "Arial";
        margin-bottom: 7px;
    }
    
    span {
        font-size: 12px;
        font-family: "Arial";
        display: block;
        margin-bottom: 52px;
    }

    input {
        width: 100%;
        border: 1px solid #000;
        height: 41px;
        margin-bottom: 21px;
        background: #f7f7f7;
    }
    button {
        margin-left: auto;
        margin-right: auto;
        width: 146px;
        height: 31px;
        border-radius: 0px;
        border: 1px solid #d8d8d8;
        background: linear-gradient(0deg, #dcdcdc, #fff);
        outline: none;
        cursor: pointer;
        font-size: 14px;
        transition: .2s;
        margin-top: 15px;

        &:hover {
            border-color : #000;
        }
    }
}



.btn {
    margin-left: auto;
    margin-right: auto;
    width: 146px;
    height: 31px;
    border-radius: 0px;
    border: 1px solid #d8d8d8;
    background: linear-gradient(0deg, #dcdcdc, #fff);
    outline: none;
    cursor: pointer;
    font-size: 14px;
    transition: .2s;
    margin-top: 15px;

    &:hover {
        border-color : #000;
    }
}