.elementcontent {
    &__btn {
        margin-bottom: 49px;
        .btn:first-child {
            margin-right: 158px;
        }
    }
}


.elementtable {
    width: 791px;

    &__head {
        display: flex;
        // justify-content: space-between;
        align-items: center;

        padding-top: 6px;
        padding-bottom: 6px;
        border-bottom: 1px solid #e6e6e6;

        padding-top: 16px;
        padding-bottom: 16px;

        span {
            margin-bottom: 0px;
        }
    }

    &__row {
        display: flex;
        // justify-content: space-between;
        align-items: center;

        padding-top: 6px;
        padding-bottom: 6px;

        border-bottom: 1px solid #e6e6e6;
    }

    input[type="checkbox"] {
        width: 16px;
        height: 16px;
    }

    span {
        font-size: 14px;
        font-family: "Arial";
    }

    picture {
        border-top: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
    }

    button {
        background: transparent;
        border: 0px;
        height: auto;
        font-size: 14px;
        color: rgba(#000, .5);
        transition: .2s;
        cursor: pointer;
        outline: 0px;

        &:hover {
            color: #000;
        }
    }

    &__tr {
        text-align: center;


        &-1 {
            // padding-left: 37px;
            width: 22%;
            // outline: 1px solid blue;
        }
        
        &-2 {
            width: 20%;

            // outline: 1px solid red;
        }
        
        &-3 {
            // outline: 1px solid green;
            width: 20%;
        }
        
        &-4 {
            width: 24%;
        }

        &-5 {
            width: 14%;
        }
    }
}


.updown {
    width: 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;

    svg {
        width: 100%;
        height: 100%;
        fill: #c6c6c6;  
        transition: .2s;

        &:hover {
            fill: #000;
        }
    }
    
    &__up {
        width: 20px;
        height: 15px;
        margin-bottom: 3px;
        cursor: pointer;
    }
    
    &__down {
        width: 20px;
        height: 15px;
        cursor: pointer;

    }
}