.admincontent {
	h3 {
		font-size: 25px;
		font-family: "Arial";
		margin-bottom: 30px;
	}

	span {
		display: block;
		font-size: 16px;
		margin-bottom: 3px;
		letter-spacing: -0.2px;
	}

	.pagination {
		margin-top: 43px;
	}
}

.pagination {
	font-size: 16px;
	display: flex;
	span {
		margin-right: 13px;
	}

	a {
		margin-right: 14px;
		color: #b25d64;

		&:hover {
			text-decoration: underline;
		}
	}
}

.listtable {
	width: 847px;
	margin-top: 49px;
	margin-left: -15px;

	@media(max-width: 1500px) {
		margin-left: 0px;
	}

	&__row {
		font-size: 16px;
		border-bottom: 1px solid #e5e5e5;
		display: flex;
		justify-content: space-between;

		padding-top: 8px;
		padding-bottom: 6px;
		padding-right: 17px;
		padding-left: 12px;

		&:first-child {
			border-top: 1px solid #e5e5e5;
		}

		span {
			font-size: 16px;
			width: 10.1%;
			line-height: initial;
		}

		a {
			font-size: 16px;
			margin-right: auto;
			width: 39%;
			line-height: initial;
			letter-spacing: -0.1px;
		}

		em {
			color: #a0a0a0;
			margin-right: auto;
			font-size: 14px;
			line-height: initial;
		}

		button {
			line-height: initial;
			color: #a0a0a0;
			background-color: transparent;
			border: 0px;
			height: auto;
			cursor: pointer;
			outline: none;
			font-size: 14px;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}
