.hamburger {
  width: 30px;
  height: 25px;
  position: relative;
  cursor: pointer;
  user-select: none;
}

.hamburger.active span {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
}

.hamburger.active:before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.hamburger.active:after {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.hamburger:after, .hamburger:before, .hamburger span {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fff;
  display: block;
  position: absolute;
  left: 0px;
  transform-origin: center center;
  transition: .2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  will-change: transform, opacity, position;
}

.hamburger::before {
  top: 0px;
}

.hamburger span {
  top: 50%;
  transform: translateY(-50%);
}

.hamburger:after {
  bottom: 0px;
}

